/* index.css - General page setup for the root entry point */

/* Global reset and body styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}

/* Fade animations */
@keyframes fadeIn {
    to {
        opacity: 1;
    }
}

/* Root div setup */
#root {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-height: 100vh;
    width: 100%;
    padding: 0 20px;
    background-color: #ffffff;
}

@keyframes fadeInSection {
    from {
        opacity: 0;
        transform: translateY(12px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

p {
    margin-bottom: 15px; /* Adjust as needed for paragraph spacing */
    line-height: 1.8; /* Ensure readability */
}

:focus {
    outline: 2px dashed #4a6fa5;
    outline-offset: 4px;
}


/* Media query */
@media (max-width: 600px) {
    body {
        background-color: #eaf0f9; /* Light blue background for small screens */
    }
    #root {
        padding: 10px;
    }
}
