/* App.css - Core component styles for the React app */

/* App container styling */
.container {
    padding: 20px;
    background-color: #ffffff; /* Ensure background matches main styles */
}

section:hover {
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.12);
    transform: translateY(-4px);
}

/* Adding smooth scroll effect for the page */
html {
    scroll-behavior: smooth;
}

hr {
    margin: 30px 0;
    border-top: 1px solid #a8c4e8;
}
